@media(min-width: 576px) {

    [data-bs-theme=light] body {
        /* config pages */
        &.body-dquam-config {
            background-color: $light;
        }

        /* dashboard pages */
        &:not(.body-dquam-config) {
            background-color: $white;

            .navbar-vertical {
                &:not(.navbar-inverted) {
                    .navbar-collapse {
                        background-color: $white;
                    }
                }
            }

            .navbar-glass {
                background-color: $white;
            }

            .content {
                background-color: $light;
                border-left: 1px solid #e8edf5;
                border-right: 1px solid #e8edf5;
            }

            .content-inner {
                border-top: 1px solid #e8edf5;
            }
        }
    }

    [data-bs-theme=dark] body {
        /* dashboard pages */
        &:not(.body-dquam-config) {
            .navbar-glass {
                background-color: #0a1727;
            }

            .content {
                background-color: #07111f;
            }
        }
    }

}



/* ========================================================================================
Utilities
=========================================================================================*/
/* ---------- size --------- */
.minw-250 {
    min-width: 250px;
}
.minw-150 {
    min-width: 150px;
}
.minw-120 {
    min-width: 120px;
}
.minw-100 {
    min-width: 100px;
}
.minw-80 {
    min-width: 80px;
}

.maxw-250 {
    max-width: 250px;
}
.maxw-150 {
    max-width: 150px;
}

/* ---------- Colors --------- */
.text-primary-emphasis {
    color: $primary-emphasis !important;
}

.bg-primary-emphasis {
    background-color: $primary-emphasis !important;
}

.text-primary-light {
    color: $primary-light !important;
}
.bg-primary-light {
    background-color: $primary-light!important;
}


/* ========================================================================================
Components
=========================================================================================*/
/* -------------------- Nav ------------------- */
@media(min-width: 1200px) {
    .navbar-vertical {
        &.navbar-expand-xl {
            .navbar-collapse {
                margin-top: 10px;

                .nav-link {
                    padding: 10px 0;

                    &.active {
                        font-weight: 600;
                    }
                }
            }
        }
    }
}


/* -------------------- Wizard (steps) ------------------- */
.theme-wizard {
    .nav-item {
        padding: 0 5px;

        .nav-link {
            font-weight: 600;
            color: $gray-400;

            [data-bs-theme=light] & {
                background-color: $light;
            }
            [data-bs-theme=dark] & {
                background-color: $gray-1100;
                color: $gray-900;
            }
    
            &.done {
                color: $gray-400;
                background-color: $gray-600;
            }
    
            &.active {
                background-color: $info;
            }
        }
    }
}
/* .theme-wizard-common-style */
.theme-wizard {
    &.theme-wizard-common-style {
        .nav-item {
            padding: 0;

            .nav-link {
                font-weight: 600;
                color: $gray-400;

                [data-bs-theme=light] & {
                    background-color: transparent;
                }

                [data-bs-theme=dark] & {
                    background-color: transparent;
                    color: $gray-900;
                }

                &.done {
                    color: $gray-400;
                    background-color: transparent;

                    .nav-item-circle-parent {
                        &:before{
                            background-color: $primary;
                        }

                        .nav-item-circle {
                            background-color: $primary !important;
                            border-color: $primary;
                        }
                    }
                }

                &.active {
                    background-color: transparent;
                    color: $gray-400 !important;

                    .nav-item-circle-parent {
                        &:after{
                            background-color: $primary;
                        }
                    }
                }

                .nav-item-circle {
                    height: 1.9rem;
                    width: 1.9rem;
                    line-height: 1.7rem;
                }
            }
        }
    }
}


/* -------------------- Avatars ------------------- */
/*on dropdown*/
.dropdown-with-avatars {
    .dropdown-toggle {
        padding: 4px 10px;
        font-size: 14px;
        box-shadow: none;

        [data-bs-theme=light] & {
            border-color: #d8e2ee;
            color: $gray-500;
        }
        [data-bs-theme=dark] & {
            border-color: #344050;
        }

        &:after {
            display: none;
        }
    }

    .dropdown-menu {
        max-height: 300px;
        overflow: auto;
    }
    
}

/* -------------------- Dropzone ------------------- */
.dropzone {
    &.dropzone-sm {
        min-height: unset;

        .dz-message {
            padding: 1.5rem;
        }
    }
}

/* -------------------- Tables ------------------- */
.table {
    font-size: 14px;

    th,
    td {
        vertical-align: middle;
    }

    [data-bs-theme=light] & th {
        color: black;
    }

    [data-bs-theme=light] &.table-white {
        background-color: $white;
    }

    .form-control {
        font-size: 14px;
    }
    .form-select {
        font-size: 14px;
    }
}


/* -------------------- Pagination ------------------- */
.pagination {
    .page-item {
        .page-link {
            min-width: 26px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: 5px;
            border: none;
            font-size: 12px;
            color: $info;

            [data-bs-theme=light] &:hover {
                background-color: $gray-300;
            }
            [data-bs-theme=dark] &:hover {
                background-color: $black ;
            }
        }

        &.active .page-link {
            [data-bs-theme=light]  & {
                background-color: $gray-300;
            }
            [data-bs-theme=dark]  & {
                background-color: $black;
            }
        }
    }
}

/* -------------------- Card ------------------- */
/*
[data-bs-theme=light] .card {
	box-shadow: 0 0 10px #eee;
}
*/

/* -------------------- Customize panel ------------------- */
.setting-toggle {
	transform: rotate(-90deg);
	margin: 0;
    border-radius: 20px 20px 0 0;

    .card-body {
        padding: 5px !important;

        .bg-primary-subtle {
            background-color: transparent !important;
        }
        .text-uppercase {
            display: none;
        }
    }
}

/* -------------------- Breadcrumb ------------------- */
.breadcrumb {
	font-size: 13px;
}


/* -------------------- Forms ------------------- */
.form-control {
    &.form-control-style-cursor {
        border-top: none;
        border-bottom: none;
        border-right: none;
        padding-top: 0;
        padding-bottom: 0;
        box-shadow: none;
        border-radius: 0;
        background-color: transparent;
    }
}

.form-select {
	box-shadow: none;

    [data-bs-theme=light] & {
        border-color: #d8e2ee;
        color: $gray-500;
    }
    [data-bs-theme=dark] & {
        color: $gray-700;
    }
}

/* icons on form inputs */
.form-control-wrapper-icon {
	position: relative;

    .form-control-icon {
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
        color: #8a8a8a;
        pointer-events: none;
    }
}

/*forms on editable cards*/
.card-editable {
    //header
    .btn-edit {
        display: none;
    }
    .btn-save {
        display: none;
    }

    &[data-mode=view] {
        .btn-edit {
            display: block;
        }
    }
    &[data-mode=edit] {
        .btn-save {
            display: block;
        }
    }

    // data
    .data-edit:not(.active) {
        display: none;
    }
    .data-view:not(.active) {
        display: none;
    }
}


/* -------------------- Buttons ------------------- */
.btn {
    &.btn-dquam-primary {
        border-color: transparent;
	    box-shadow: none;
        background-color: $primary;
        color: $white;

        &:hover {
            background-color: $primary-emphasis;
        }
    }

    &.btn-dquam-primary-light {
        border-color: transparent;
	    box-shadow: none;
        background-color: $primary-light;
        color: $white;

        &:hover {
            background-color: $primary;
        }
    }

    &.btn-dquam-primary-emphasis {
        border-color: transparent;
	    box-shadow: none;
        background-color: $primary-emphasis;
        color: $white;

        &:hover {
            background-color: $primary;
        }
    }
    
    &.btn-dquam-success {
        border-color: transparent;
	    box-shadow: none;
        background-color: $success;
        color: $white;

        &:hover {
            background-color: $success-emphasis;
        }
    }

    &.btn-dquam-info {
        border-color: transparent;
	    box-shadow: none;
        background-color: $info;
        color: $white;

        &:hover {
            background-color: $info-emphasis;
        }
    }

    &.btn-dquam-danger {
        border-color: transparent;
	    box-shadow: none;
        background-color: $danger;
        color: $white;

        &:hover {
            background-color: $danger-emphasis;
        }
    }

    &.btn-dquam-secondary {
        border-color: transparent;
	    box-shadow: none;
        background-color: $secondary;
        color: $white;

        &:hover {
            background-color: $gray-600;
        }
    }
}


[data-bs-theme=light] .btn {
    &.btn-link {
        &:focus {
            background-color: #e6e6e6;
        }
    }
}
[data-bs-theme=dark] .btn {
    &.btn-link {
        &:focus {
            background-color: #0a1523;
        }
    }
}

.btn-close-falcon {
    &:before,
    &::after {
        background-color: $danger;
    }
}

/* ========================================================================================
Configuration Pages
=========================================================================================*/
/* ---------- Steps Wizard ---------- */
.card-header-config-stages {
    
    .nav-item {
        width: 33.33%;
        padding: 0;

        .nav-link {
            color: rgba(126, 85, 164, 0.35) !important;
    
            &.active {
                color: $primary !important;
                background-color: transparent;

                .nav-item-circle-parent {
                    &:after {
                        background-color: $gray-600;
                    }
                }
            }
            &.done {
                color: $gray-600 !important;
                background-color: transparent;

                .nav-item-circle-parent {
                    .nav-item-circle {
                        background-color: $gray-600 !important;
                        border: $gray-600;
                    }

                    &:before {
                        background-color: $gray-600;
                    }
                }
            }
        }
    }
    
}
